*,*::after , *::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
}

body {
    width: 100%;
    height: 100%;
    background-color:black;
    font-family: sans-serif;
    justify-content: center;
    flex-wrap: wrap;
}

a {
    text-decoration: none;
    color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
span {
    font-size: 18px;
    color: #fff;
    font-family: sans-serif
}

li {
    overflow: hidden;
}

span {
    display: block;
}

p {
    font-size: 16px;
    font-family: sans-serif;
}

.white {
    color: #fff;
}

.black {
    color: black;
}

.d_flex {
    display: flex;
}

.align_items_center {
    align-items: center;
}

.justify_content_center {
    justify-content: center;
}

.justify_content_space_between {
    justify-content: space-between;
}

.mx_15 {
    margin: 0px 15px;
}
.mx_20 {
    margin: 0px 20px;
}

.px_30 {
    padding: 0px 10px;
}

.p_30 {
    padding: 30px;
}


.container {
    width: 92%;
    margin: 0px auto;
}

.pz_10 {
    padding: 10px 0px;
}

.sweet {
    color: #F9004D;
}

.row {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    overflow: hidden;
    align-items: flex-start;
    justify-content: center;
}

.col_2 {
    max-width: 50%;
    display: flex;
    padding: 20px;
    justify-content: center;
    flex-direction: column;
}

.col_3 {
    max-width: 33.3333333333%;
    height: auto;
    display: flex;
    padding: 20px;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
   
}
.col_4 {
    max-width: 25%;
    display: flex;
    padding: 20px;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
}
.col_5{
    max-width: 20%;
    display: flex;
    padding: 20px;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
}
.col_10{
    max-width: 15%;
    display: flex;
    padding: 5px;
    justify-content: center;
    flex-direction: column;
    margin-top: 15px;
}

.component__space {
    padding: 100px 0px;
}

.pointer {
    cursor: pointer;
}

svg {
    stroke-width: 1!important;
}

.relative {
    position: relative;
}

.absolute {
    position: absolute;
}

strong {
    color: #F9004D;
    font-weight: 600;
}

input {
    color: rgba(198, 201, 216, .75);
}

button.btn {
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    border: 2px solid #f9004d;
    background-color: #f9004d;
    color: #fff!important;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .4s all ease;
    height: 50px;
    width: 180px;
    margin: 0px 10px;
}

.p_color {
    color: rgba(198, 201, 216, .75);
    font-size: 18px;
    line-height: 30px;
    font-family: sans-serif;
    margin-bottom: 15px;
}

button.about.btn.pointer:hover {
    background: #fff;
    color: #000!important;
}
.tech_style{
    display: flex;
}


